body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header-container .login-button, .content-container .bottom-buttons .edit-button, .content-container .bottom-buttons .delete-button {
  margin: 1px 8px;
  cursor: pointer;
  color: #fff;
  padding: 5px 22px;
  font-size: 16px; }

.header-container, .post-container {
  box-shadow: 1px 1px 3px 1px #8f8f8f; }

h2 {
  margin: 20px 10px; }

h3 {
  margin: 13px 0; }

.header-container {
  background-color: #0c67a0;
  max-width: 100%;
  padding: 5px 2vw;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  margin-bottom: 10px; }
  .header-container .add-icon:hover {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg); }
  .header-container .header-title i {
    margin-right: 3px; }
  .header-container a {
    color: #fff; }
  .header-container .header-login {
    color: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center; }
  .header-container .login-button {
    padding: 2px 9px;
    background-color: #313131; }
    .header-container .login-button:hover {
      background-color: #515151; }

.main-container {
  margin: 2vh 2vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  background-color: #ffffff;
  min-height: 100vh;
  font-size: 16px;
  color: #2e2e2e; }

.content-container {
  max-width: 60vw;
  -webkit-animation: fadeInContent 0.4s;
          animation: fadeInContent 0.4s; }
  @media screen and (max-width: 720px) {
    .content-container {
      max-width: 100vw; } }
  .content-container .bottom-buttons {
    margin-top: 20px;
    display: -webkit-flex;
    display: flex; }
    .content-container .bottom-buttons .edit-button {
      background-color: #1cc546; }
      .content-container .bottom-buttons .edit-button:hover {
        background-color: #1fc048; }
      .content-container .bottom-buttons .edit-button:disabled {
        background-color: #60e782; }
    .content-container .bottom-buttons .delete-button {
      background-color: #d83737; }
      .content-container .bottom-buttons .delete-button:hover {
        background-color: #ee3b3b; }

.post-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 2px 12px;
  margin: 10px 0; }
  .post-container .post-header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: flex-start;
            align-items: flex-start; }
  .post-container .post-info {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    font-style: italic; }
    .post-container .post-info .author-info::after {
      content: "|";
      margin: 0px 10px; }
    .post-container .post-info i {
      margin-right: 3px; }
  .post-container .buttons {
    margin: 5px 0;
    display: -webkit-flex;
    display: flex; }
    .post-container .buttons .delete-icon {
      color: red; }
    .post-container .buttons .edit-icon {
      color: darkblue; }
    .post-container .buttons i {
      margin: 2px;
      cursor: pointer; }
      .post-container .buttons i:hover {
        -webkit-transform: rotate(-5deg);
                transform: rotate(-5deg);
        text-shadow: 0px 0px 1px #c5c5c5; }

.input-title {
  border-radius: 3px;
  padding: 3px;
  font-size: 1.17em;
  font-weight: bold;
  margin: 10px 0;
  border-color: lightgray; }

.input-content {
  margin: 20px 0;
  padding: 5px;
  font-size: 16px;
  border-radius: 3px;
  border-color: lightgray; }

.error-message {
  background-color: #f2dede;
  margin: 10px 0;
  border-radius: 3px;
  padding: 2px 10px;
  font-weight: bold; }

.footer-container {
  text-align: center;
  margin: 40px 0 10px;
  font-weight: bold; }

.message-container {
  text-align: center; }

.text-small {
  font-size: 80%; }

.spinner {
  max-width: 30px;
  max-height: 30px;
  margin: 10px auto;
  -webkit-animation: spinnerRotate 1s linear infinite;
          animation: spinnerRotate 1s linear infinite; }

@-webkit-keyframes fadeInContent {
  from {
    margin-top: 10vh;
    -webkit-filter: blur(2px);
            filter: blur(2px);
    opacity: 0; } }

@keyframes fadeInContent {
  from {
    margin-top: 10vh;
    -webkit-filter: blur(2px);
            filter: blur(2px);
    opacity: 0; } }

@-webkit-keyframes spinnerRotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spinnerRotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

